@font-face {
  font-family: "antonio-bold";
  src: url("fonts/antonio/antonio-bold-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-bold-webfont.woff") format("woff");
}
@font-face {
  font-family: "antonio-light";
  src: url("fonts/antonio/antonio-light-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-light-webfont.woff") format("woff");
}
@font-face {
  font-family: "antonio-regular";
  src: url("fonts/antonio/antonio-regular-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "great-vibes-regular";
  src: url("fonts/great_vibes/great-vibes-regular-webfont.woff2") format("woff2"), url("fonts/great_vibes/great-vibes-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold-italic";
  src: url("fonts/lora/lora-bold-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold";
  src: url("fonts/lora/lora-bold-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-italic";
  src: url("fonts/lora/lora-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-regular";
  src: url("fonts/lora/lora-regular-webfont.woff2") format("woff2"), url("fonts/lora/lora-regular-webfont.woff") format("woff");
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  line-height: 1;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}
:focus {
  outline: none;
}
p,
dd,
dl,
figure,
blockquote {
  margin: 0;
}
blockquote,
q {
  quotes: none;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
audio,
video {
  display: block;
}
img {
  display: inline-block;
  border: none;
}
iframe {
  border: none;
}
pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}
a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}
abbr {
  border: none;
  text-decoration: none;
}
b,
strong {
  font-weight: inherit;
}
i,
em {
  font-style: inherit;
}
dfn {
  font-style: inherit;
}
mark {
  background-color: transparent;
  color: inherit;
}
small {
  font-size: inherit;
}
button,
input,
optgroup,
select,
textarea {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-align: left;
  text-transform: none;
}
button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  appearance: none;
}
textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
}
button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0;
}
button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none;
}
[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}
[type='search']::-webkit-search-decoration {
  appearance: none;
}
[type='search'] {
  outline: none;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
legend {
  display: block;
  padding: 0;
  white-space: normal;
}
select {
  appearance: none;
}
select::-ms-expand {
  display: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
::-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:focus::-webkit-input-placeholder {
  opacity: 0;
}
:focus::-moz-placeholder {
  opacity: 0;
}
:focus:-moz-placeholder {
  opacity: 0;
}
:focus:-ms-input-placeholder {
  opacity: 0;
}
svg {
  display: block;
  width: 100%;
  height: 100%;
}
[hidden] {
  display: none;
}
:disabled,
.disabled {
  cursor: not-allowed;
}
::-ms-clear {
  display: none;
}
.visually-hidden {
  position: absolute;
  z-index: -$z-index1;
  width: 0;
  height: 0;
  margin: 0;
  border: none;
  overflow: hidden;
}
body {
  font: 14px/24px 'lora-regular', sans-serif, arial;
  color: #797979;
}
.container {
  width: 98%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
h1 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
h2 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
h3 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
h4 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
h5 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
h6 {
  font: 48px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
.sect__title {
  margin-bottom: 52px;
  font-size: 14px;
  letter-spacing: 0.7px;
}
.btn {
  display: inline-block;
  font: 14px 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #fff;
  border-radius: 25px;
}
.btn_type_reservation {
  height: 40px;
  padding: 0 32px;
  line-height: 40px;
  letter-spacing: 0.5px;
  border: 2px solid #fff;
}
.btn_type_reservation:hover {
  color: #efba39;
  border-color: #efba39;
}
.btn_type_more {
  height: 50px;
  padding: 0 52px;
  line-height: 54px;
  letter-spacing: 1.5px;
  background-color: #ef4a09;
}
.page__hero {
  position: relative;
  z-index: 1;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  background: url("img/content/homepage/annie-spratt-464747-unsplash_1920px.png") no-repeat center/cover;
}
.page__hero .container {
  text-align: center;
}
.page__hero::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
  content: '';
}
.page__headline {
  margin-bottom: 24px;
  font: 70px/1 'antonio-regular', sans-serif, arial;
  color: #fff;
}
.page__headline span {
  font: 120px/110px 'great-vibes-regular', sans-serif, arial;
  text-transform: initial;
  color: #efba39;
}
.page__inf {
  padding: 30px 50px;
}
.page__phone {
  margin-bottom: 12px;
  font: 14px/1 'antonio-regular', sans-serif, arial;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #fff;
}
.page__phone span {
  display: inline-block;
  margin-left: 6px;
  color: #efba39;
}
.homepage-about__inner {
  padding: 150px 0;
}
.homepage-about__row {
  display: flex;
}
.homepage-about__col {
  flex-basis: 50%;
}
.homepage-about__cont p:not(:last-child) {
  margin-bottom: 24px;
}
.homepage-about__title {
  margin-bottom: 20px;
}
.homepage-about__text {
  margin-bottom: 31px;
}
.homepage-about__btn_type_more {
  height: 42px;
  padding: 0 48px;
  line-height: 50px;
}
.homepage-feat__inner {
  padding: 30px 0 150px 0;
}
.homepage-feat__top,
.homepage-blog__top {
  display: flex;
  margin-bottom: 42px;
  align-items: center;
  justify-content: space-between;
}
.homepage-feat__top .sect__title,
.homepage-blog__top .sect__title {
  margin-bottom: 0;
}
.homepage-feat__menu-page,
.homepage-blog__blog-page {
  font-family: 'lora-italic', sans-serif, arial;
}
.homepage-feat__menu-page [name="arrow-forward"],
.homepage-blog__blog-page [name="arrow-forward"] {
  vertical-align: -3px;
}
.homepage-feat__row {
  display: flex;
}
.homepage-feat__col {
  flex-basis: 50%;
}
.homepage-feat__header {
  margin-bottom: 46px;
}
.homepage-feat__title {
  margin-bottom: 19px;
}
.homepage-feat__switch {
  display: flex;
  align-items: center;
}
.homepage-feat__switch li:not(:last-child) {
  margin-right: 26px;
}
.homepage-feat__switch button {
  position: relative;
  z-index: 1;
}
.homepage-feat__switch button.active {
  color: #000;
}
.homepage-feat__switch button.active::after {
  content: '';
  position: absolute;
  bottom: 20%;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 5px;
  background-color: #efba39;
}
.homepage-feat__col:not(:last-child) {
  margin-right: 30px;
}
.homepage-feat__dish:not(:last-child) {
  margin-bottom: 30px;
}
.homepage-dish {
  display: flex;
}
.homepage-dish__header {
  position: relative;
  margin-bottom: 12px;
  padding-bottom: 12px;
}
.homepage-dish__header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 30px;
  height: 2px;
  display: block;
  background-color: #efba39;
}
.homepage-dish__pic {
  position: relative;
  flex-basis: 200px;
  margin-right: 30px;
  padding-top: 150px;
  background-color: #797979;
  overflow: hidden;
}
.homepage-dish__pic img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.homepage-dish__desc {
  padding: 30px 0 22px 0;
}
.homepage-dish__name {
  margin-bottom: 6px;
  font-size: 18px;
  text-transform: initial;
}
.homepage-dish__price {
  font: 14px/1 'antonio-regular', sans-serif, arial;
}
.homepage-dish__comp {
  font-size: 14px;
}
.homepage-reserv__inner {
  padding-bottom: 66px;
}
.homepage-reserv__title {
  margin-bottom: 30px;
}
.homepage-reserv__reserv {
  position: relative;
  z-index: 1;
  padding: 63px 72px 70px 72px;
  background: #797979 url("img/content/homepage/nick-karvounis-463853-unsplash_1170px.png") no-repeat center/cover;
}
.homepage-reserv__reserv::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
}
.homepage-reserv__nb {
  margin-bottom: 22px;
  color: #fff;
}
.homepage-reserv__nb span {
  color: #efba39;
}
.homepage-reserv__row {
  clear: both;
}
@supports (display: flex) {
  .homepage-reserv__row {
    display: flex;
  }
}
.homepage-reserv__col {
  float: left;
  width: 50%;
}
.homepage-reserv__col:not(:last-child) {
  padding-right: 54px;
}
@supports (display: flex) {
  .homepage-reserv__col {
    flex-basis: 50%;
  }
}
.homepage-form__rows {
  margin-bottom: 30px;
}
.homepage-form__row {
  clear: both;
}
.homepage-form__row:not(:last-child) {
  margin-bottom: 34px;
}
@supports (display: flex) {
  .homepage-form__row {
    display: flex;
  }
}
.homepage-form__col {
  float: left;
}
.homepage-form__col_size_md {
  width: 50%;
}
@supports (display: flex) {
  .homepage-form__col_size_md {
    flex-basis: 50%;
  }
}
.homepage-form__col_size_lg {
  width: 100%;
}
@supports (display: flex) {
  .homepage-form__col_size_lg {
    flex-basis: 100%;
  }
}
.homepage-form__col:not(:last-child) {
  padding-right: 10px;
}
.homepage-form__label {
  display: block;
  margin-bottom: 10px;
  font: 14px/1 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #fff;
}
.homepage-form__input {
  width: 100%;
  padding: 7px 20px;
  background-color: #fff;
}
.homepage-form__submit {
  height: 39px;
  padding: 0 36px;
  font-size: 12px;
  line-height: 40px;
}
.homepage-reserv__map {
  position: relative;
}
.homepage-reserv__pic {
  position: absolute;
  top: 6px;
  left: 3%;
  width: 97.5%;
  padding-top: 408px;
  background-color: #808080;
}
.homepage-blog__blog-page [name="arrow-forward"] {
  vertical-align: -3px;
}
.homepage-blog__inner {
  padding: 150px 0 143px 0;
}
.homepage-blog__title {
  margin-bottom: 32px;
}
.homepage-blog__posts {
  display: flex;
}
.homepage-blog__post:not(:last-child) {
  margin-right: 30px;
}
.homepage-post {
  flex: 1;
}
.homepage-post__pic {
  position: relative;
  margin-bottom: 23px;
  padding-top: 270px;
  background-color: #797979;
  overflow: hidden;
}
.homepage-post__pic img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.homepage-post__name {
  margin: 9px 0 24px 0;
  font-size: 18px;
  text-transform: initial;
}
.homepage-post__text {
  margin-bottom: 26px;
}
.homepage-post__continue {
  font: 14px/1 'antonio-regular', sans-serif, arial;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #000;
}
.homepage-post__continue ion-icon {
  margin-left: 6px;
  vertical-align: -1px;
}
header {
  display: flex;
  align-items: center;
  padding: 27px 50px;
}
.header__logo {
  flex: 2;
}
.page__hero {
  background-color: #808080;
}
.logo {
  text-transform: uppercase;
}
.logo h2 {
  font-size: 38px;
  line-height: 38px;
  color: #fff;
}
.logo p {
  font: 9px 'antonio-regular', sans-serif, arial;
  line-height: 9px;
  color: #fff;
}
.nav ul {
  display: flex;
  align-items: center;
}
.nav li {
  margin-right: 46px;
}
.nav a {
  font: 14px/48px 'antonio-regular', sans-serif, arial;
  text-transform: uppercase;
  color: #fff;
}
.nav a:hover {
  color: #efba39;
}
.social {
  display: flex;
  align-items: center;
}
.social li:not(:last-child) {
  margin-right: 11px;
}
.social a {
  display: block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 36px;
  color: #fff;
  background-color: #797979;
  border-radius: 50%;
}
.social a:hover {
  background-color: #efba39;
}
footer {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 99px 50px 86px 50px;
  background: url("img/content/homepage/annie-spratt-464747-unsplash_1920px.png") no-repeat bottom/cover;
}
footer::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  content: '';
}
.footer__row {
  display: flex;
}
.footer__col .footer__name {
  margin-bottom: 20px;
  font: 12px/1 'antonio-regular', sans-serif, arial;
  color: #797979;
}
.footer__col li {
  font-size: 12px;
  color: #fff;
}
.footer__col:not(:last-child) {
  margin-right: 156px;
}
